<template>
  <div id="wrapper">
    <nav class="navbar is-black">
      <div class="navbar-brand">
        <router-link to="/" class="navbar-item">
          <strong>TGHomie</strong></router-link
        >

        <a
          class="navbar-burger"
          aria-label="menu"
          aria-expanded="false"
          data-target="navbar-menu"
          @click="showMobileMenu = !showMobileMenu"
        >
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
          <span aria-hidden="true"></span>
        </a>
      </div>

      <div
        class="navbar-menu"
        id="navbar-menu"
        :class="{ 'is-active': showMobileMenu }"
      >
        <div class="navbar-start">
          <div class="navbar-item">
            <form method="get" action="/search">
              <div class="field has-addons">
                <div class="control">
                  <input
                    type="text"
                    class="input"
                    placeholder="What are you looking for?"
                    name="query"
                  />
                </div>

                <div class="control">
                  <button class="button is-success">
                    <span class="icon"> <i class="fas fa-search"></i></span>
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
        <div class="navbar-end">
          <router-link to="/games" class="navbar-item">Games</router-link>
          <router-link to="/productivity" class="navbar-item">Productivity</router-link>
          <router-link to="/health-and-fitness" class="navbar-item">Health and Fitness</router-link>
          <router-link to="/education" class="navbar-item">Education</router-link>
          <router-link to="/kids" class="navbar-item">Kids</router-link>

          <div class="navbar-item">
            <div class="buttons">
              <template v-if="store.isAuthenticated">
                <router-link to="/my-account" class="button is-light"
                  >My Account</router-link
                ></template
              >
              <template v-else>
                <router-link to="/log-in" class="button is-light"
                  >Login</router-link
                ></template
              >

              <router-link to="/cart" class="button is-success">
                <span class="icon"><i class="fas fa-shopping-cart"></i></span>
                <span>Cart({{ cartTotalLength }})</span></router-link
              >
            </div>
          </div>
        </div>
      </div>
    </nav>

    <div
      class="is-loading-bar has-text-centered"
      :class="{ 'is-loading': store.isLoading }"
    >
      <div class="lds-dual-ring"></div>
    </div>

    <section class="section">
      <router-view />
    </section>

    <footer class="footer">
      <p class="has-text-centered">Copyright ©️2022 TGHomie Inc. All right reserved. </p>
    </footer>
  </div>
</template>

<script setup>
import http from "@/utils/http";
import { ref, computed, onMounted } from "vue";
import { useStore } from "./store";

const showMobileMenu = ref(false);
const store = useStore();
let cart = ref({ items: [] });
const token = store.token;

store.initializeStore(store.cart);
if (token) {
  http.defaults.headers.common["Authorization"] = "Token " + token;
} else {
  http.defaults.headers.common["Authorization"] = "";
}

const cartTotalLength = computed(() => {
  let totalLength = 0;
  for (let i = 0; i < cart.value.items.length; i++) {
    totalLength += cart.value.items[i].quantity;
  }
  return totalLength;
});

onMounted(() => {
  cart.value.items = store.cart.items;
});
</script>

